import normalizeWheel from 'normalize-wheel';
import {TweenLite} from 'gsap';

export class Parallax {
	constructor() {
		this.scrollable = $('#scrollable');
		this.elem = $('#parallax');
		this.speed = $('#parallax').data('speed');
		this.currentY = 0;
		this.moveElem();
	}

	moveElem() {
		let animateMove = (e) => {
			this.norm = normalizeWheel(e);
			this.currentY += this.norm.spinY * 10;
			this.maxHeight = this.scrollable.height() - $(window).height();
			if (this.currentY < 0) {
				this.currentY = 0;
			}
			if (this.currentY * 10 > this.maxHeight) {
				this.currentY = this.maxHeight / 10;
			}

			TweenLite.to(this.elem, 0.8, {
				y: this.currentY * this.speed,
				ease: Expo.easeOut,
			});
		};

		document.addEventListener('mousewheel', animateMove);
		document.addEventListener('DOMMouseScroll', animateMove);
	}
}
