export class Sticky {
	constructor() {
		this.elements = $('[data-sticky]');
		this.moveSticky();
		this.x = 0;
		this.y = 0;
	}

	moveSticky() {
		this.elements.each((index, elem) => {
			elem.addEventListener('mousemove', (e) => {
				this.x = e.offsetX * 0.2;
				this.y = e.offsetY * 0.15;

				TweenLite.to(elem, 1, {
					x: this.x,
					y: this.y,
				});
			});
			elem.addEventListener('mouseleave', () => {
				TweenLite.to(elem, 1, {
					x: 0,
					y: 0,
				});
			});
		});
	}
}
