import {Expo} from 'gsap';

export class RollerSlider {
	constructor() {
		this.counter = 1;
		this.time = 2;
		this.activeElement = $('.active-content');
		this.length = $('.heading-content').length;
		this.moveSlider();
	}

	moveSlider() {
		let tl = new TimelineMax({onComplete: this.moveSlider.bind(this)});
		let elemTo = $(`[data-slider=${this.counter}]`);

		$('#slider-length').html(`0${this.length}`);
		$('#slider-number').html(`0${this.counter}`);

		tl
			.set(elemTo, {className: '+= active-content'})
			.to(elemTo.find('.heading-content-title-label__item_1'), 2, {
				y: 0,
				ease: Expo.easeOut,
			})
			.to(elemTo.find('.heading-content-title-label__item_2'), 2, {
				y: 0,
				ease: Expo.easeOut,
			}, '0.1')
			.to(elemTo.find('.heading-content-description-label__item_1'), 2.5, {
				y: 0,
				ease: Expo.easeOut,
			}, '0.3')
			.to(elemTo.find('.heading-content-description-label__item_2'), 2.5, {
				y: 0,
				ease: Expo.easeOut,
			}, '0.3')
			.to($('.counter__line_active'), this.time, {
				x: '0%',
			})
			.to(elemTo.find('.heading-content-title-label__item_1'), 1, {
				y: -150,
				ease: Power3.easeIn,
			})
			.to(elemTo.find('.heading-content-title-label__item_2'), 1, {
				y: -150,
				ease: Power3.easeIn,
			}, '-=0.9')
			.to(elemTo.find('.heading-content-description-label__item_1'), 1, {
				y: -30,
				ease: Power3.easeIn,
			}, '-=1')
			.to(elemTo.find('.heading-content-description-label__item_2'), 1, {
				y: -30,
				ease: Power3.easeIn,
			}, '-=1')
			.set(elemTo.find('.heading-content-title-label__item_1'), {y: 120})
			.set(elemTo.find('.heading-content-title-label__item_2'), {y: 120})
			.set(elemTo.find('.heading-content-description-label__item_1'), {y: 18})
			.set(elemTo.find('.heading-content-description-label__item_2'), {y: 18})
			.set($('.counter__line_active'), {x: '-100%'});

		this.counter += 1;
		if (this.counter > this.length) {
			this.counter = 1;
		}
	}
}
