import {TweenLite} from 'gsap';

export class GallerySlider {
	constructor() {
		this.elems = document.querySelectorAll('#gallery');
		this.currentX = 0;
		this.moveSlider();
		this.currentPosX = 0;
		this.nextPosX = 0;
	}

	moveSlider() {
		this.elems.forEach((elem) => {
			elem.onmousedown = (e) => {
				this.currentPosX = e.pageX;
				let moveAt = (e) => {
					this.nextPosX = e.pageX;
					if (this.currentPosX > this.nextPosX) {
						this.currentX -= 100;
					} else {
						this.currentX += 100;
					}

					if (this.currentX < -1500) {
						this.currentX = -1500;
					}

					if (this.currentX > 0) {
						this.currentX = 0;
					}
					TweenLite.to(elem, 1, {
						x: this.currentX,
						ease: Expo.easeOut,
					});
				};

				document.addEventListener('mousemove', moveAt);

				elem.onmouseup = () => {
					document.removeEventListener('mousemove', moveAt);
				};
			};
		});
	}
}
