import normalizeWheel from 'normalize-wheel';

export class CustomScroll {
	constructor() {
		this.scrollable = $('#scrollable');
		this.currentY = 0;
		this.maxHeight = 0;
		this.scrollPage();
	}

	scrollPage() {
		let animateScroll = (e) => {
			this.norm = normalizeWheel(e);
			this.currentY += this.norm.spinY * 100;
			this.maxHeight = this.scrollable.height() - $(window).height();
			this.scrollY = this.currentY / this.maxHeight * 100;

			if (this.currentY < 0) {
				this.currentY = 0;
			}

			if (this.currentY > this.maxHeight) {
				this.currentY = this.maxHeight;
			}

			TweenLite.to(this.scrollable, 0.8, {
				y: -this.currentY,
				ease: Power4.easeOut,
				overwrite: 'preexisting',
			});
			TweenLite.to($('#scroll-line'), 0.8, {
				y: `${this.scrollY}%`,
				ease: Power4.easeOut,
				overwrite: 'preexisting',
			});
		};

		document.addEventListener('mousewheel', animateScroll);
		document.addEventListener('DOMMouseScroll', animateScroll);
	}
}
